@keyframes bounce-top {
  0% {
    transform: translateY(-45px);
    animation-timing-function: ease-in;
    opacity: 1;
  }
  24% {
    opacity: 1;
  }
  40% {
    transform: translateY(-24px);
    animation-timing-function: ease-in;
  }
  65% {
    transform: translateY(-12px);
    animation-timing-function: ease-in;
  }
  82% {
    transform: translateY(-6px);
    animation-timing-function: ease-in;
  }
  93% {
    transform: translateY(-4px);
    animation-timing-function: ease-in;
  }
  25%,
  55%,
  75%,
  87% {
    transform: translateY(0);
    animation-timing-function: ease-out;
  }
  to {
    transform: translateY(0);
    animation-timing-function: ease-out;
    opacity: 1;
  }
}
@keyframes rotate-90-cw {
  0% {
    transform: rotate(0);
  }
  to {
    transform: rotate(90deg);
  }
}
.landingHeroChild,
.theEasiestWay {
  align-self: stretch;
  position: relative;
}
.landingHeroChild {
  flex: 1;
  border-radius: var(--br-5xs);
  max-width: 100%;
  overflow: hidden;
  max-height: 100%;
  object-fit: cover;
}
.theEasiestWay {
  margin: 0;
  font-size: inherit;
  line-height: 120%;
  font-weight: 700;
  font-family: inherit;
}
.join {
  font-weight: 300;
}
.join,
.payola {
  font-family: var(--font-roboto);
}
.joinPayolaTodayAndDiscover {
  margin: 0;
}
.hundredOrA {
  text-decoration: underline;
}
.joinPayolaTodayContainer {
  margin: 0;
  align-self: stretch;
  position: relative;
  font-size: var(--font-size-xl);
  line-height: 1.94rem;
  color: var(--color-black);
}
.buttonGroup,
.frameGroup,
.theEasiestWayToGenerateInParent {
  align-self: stretch;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  gap: var(--gap-12xl);
}
.buttonGroup,
.frameGroup {
  flex-direction: row;
  justify-content: center;
  gap: var(--gap-16xl);
}
.frameGroup {
  flex-direction: column;
  justify-content: flex-start;
  gap: var(--gap-5xl);
  z-index: 0;
}
.vectorIcon,
.vectorIcon1 {
  position: absolute;
  object-fit: cover;
}
.vectorIcon {
  top: 0;
  left: 1.15rem;
  width: 11.98rem;
  height: 10.08rem;
}
.vectorIcon1 {
  top: 6.37rem;
  left: 0.28rem;
  width: 2.32rem;
  height: 2.05rem;
}
.vectorParent {
  position: absolute;
  margin: 0 !important;
  top: -2.81rem;
  left: 12.45rem;
  width: 13.12rem;
  height: 10.08rem;
  opacity: 1;
  z-index: 1;
}
.vectorParent.animate {
  animation: 8s ease-in-out 0s infinite alternate-reverse both bounce-top;
}
.vectorIcon2,
.vectorIcon3 {
  position: absolute;
  top: 0.91rem;
  left: 1.02rem;
  width: 15.46rem;
  height: 13.82rem;
  object-fit: cover;
}
.vectorIcon3 {
  top: 3.66rem;
  left: 1.67rem;
  width: 1.21rem;
  height: 1.42rem;
}
.vectorGroup {
  position: absolute;
  margin: 0 !important;
  top: 25.58rem;
  left: 0.35rem;
  width: 17.39rem;
  height: 15.28rem;
  opacity: 1;
  z-index: 2;
}
.vectorGroup.animate {
  animation: 5s ease-in-out 0s infinite alternate forwards rotate-90-cw;
}
.frameParent {
  align-self: stretch;
  flex: 1;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  position: relative;
  gap: var(--gap-xl);
}
.landingHero {
  position: absolute;
  top: 0;
  left: 0;
  border-radius: var(--br-5xs);
  background-color: var(--dominant);
  box-shadow: 0 3px 16px rgba(0, 0, 0, 0.08);
  width: 83.13rem;
  height: 35.38rem;
  display: flex;
  flex-direction: row;
  padding: var(--padding-base) var(--padding-3xs);
  box-sizing: border-box;
  align-items: flex-start;
  justify-content: flex-start;
  gap: var(--gap-5xl);
  margin-top: 4.69rem;
  text-align: center;
  font-size: var(--font-size-21xl);
  color: var(--dark-gray);
  font-family: var(--text-small);
}
.componentParent {
  position: relative;
  width: 83.13rem;
  height: 39.63rem;
}
@media screen and (max-width: 1200px) {
  .frameParent {
    flex: 1;
  }
  .landingHero {
    width: auto;
    align-self: unset;
    align-items: center;
    justify-content: flex-start;
    padding-left: var(--padding-71xl);
    box-sizing: border-box;
    z-index: 1;
  }
  .componentParent {
    align-self: stretch;
    width: auto;
  }
}
@media screen and (max-width: 960px) {
  .landingHeroChild {
    max-width: 100%;
    max-height: 100%;
  }
  .theEasiestWay {
    font-size: var(--font-size-6xl);
    text-align: center;
  }
  .joinPayolaTodayContainer {
    font-size: var(--font-size-mini);
  }
  .buttonGroup,
  .frameGroup {
    flex-direction: column;
  }
  .frameGroup {
    max-width: 100%;
  }
  .landingHero {
    background-color: var(--dominant);
    width: auto;
    align-self: unset;
    height: auto;
    flex-direction: row;
  }
  .componentParent {
    align-self: stretch;
    width: auto;
  }
}
@media screen and (max-width: 420px) {
  .landingHeroChild {
    max-width: 100%;
    flex: unset;
    align-self: stretch;
  }
  .buttonGroup,
  .frameParent {
    align-items: center;
    box-sizing: border-box;
    align-self: stretch;
  }
  .buttonGroup {
    width: auto;
    flex: 1;
    flex-direction: row;
    gap: var(--gap-xl);
    justify-content: space-between;
    padding-top: 0;
    padding-bottom: 0;
  }
  .frameParent {
    flex: unset;
    justify-content: flex-start;
    padding-left: var(--padding-3xs);
    padding-right: var(--padding-3xs);
  }
  .landingHero {
    height: auto;
    flex-direction: column;
    padding-left: 0;
    padding-top: var(--padding-base);
    padding-right: 0;
    box-sizing: border-box;
  }
  .componentParent {
    align-self: stretch;
    width: auto;
    min-height: 50rem;
  }
}
@media screen and (min-width: 950px) and (max-width: 500px) {
  .landingHero {
    gap: var(--gap-0);
    padding-left: var(--padding-xl);
    padding-right: var(--padding-xl);
    box-sizing: border-box;
  }
}
