.home {
  cursor: pointer;
  border: 0;
  padding: 0;
  background-color: transparent;
  position: relative;
  font-size: var(--text-medium-size);
  font-family: var(--font-roboto);
  color: var(--dominant);
  text-align: center;
  display: inline-block;
}
.footerLinks {
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: flex-start;
  gap: var(--gap-3xl);
}
.vectorIcon {
  position: absolute;
  height: 96%;
  width: 96%;
  top: 2%;
  right: 2%;
  bottom: 2%;
  left: 2%;
  max-width: 100%;
  overflow: hidden;
  max-height: 100%;
  object-fit: cover;
}
.entypoSocialfacebookWithCi,
.entypoSocialinstagramWithC,
.entypoSocialtwitterWithCir {
  text-decoration: none;
  position: absolute;
  top: 0;
  left: 0;
  width: 30px;
  height: 30px;
  overflow: hidden;
}
.entypoSocialinstagramWithC,
.entypoSocialtwitterWithCir {
  left: 100px;
}
.entypoSocialinstagramWithC {
  left: 50px;
}
.socialIcons {
  position: relative;
  width: 130px;
  height: 30px;
}
.footerLinksParent,
.typewithIcons {
  display: flex;
  flex-direction: row;
  align-items: center;
}
.footerLinksParent {
  flex: 1;
  justify-content: space-between;
}
.typewithIcons {
  background-color: var(--studio-darkmode-maincta-457eff);
  width: 1440px;
  padding: var(--padding-13xl) var(--padding-21xl);
  box-sizing: border-box;
  justify-content: center;
}
@media screen and (max-width: 1200px) {
  .typewithIcons {
    padding: var(--padding-11xl) var(--padding-61xl);
    box-sizing: border-box;
  }
}
@media screen and (max-width: 960px) {
  .footerLinks {
    display: none;
  }
  .typewithIcons {
    padding: var(--padding-10xl) var(--padding-21xl);
    box-sizing: border-box;
  }
}
@media screen and (max-width: 420px) {
  .typewithIcons {
    padding: var(--padding-9xl) var(--padding-5xl);
    box-sizing: border-box;
  }
}
