.frameChild {
  align-self: stretch;
  position: relative;
  max-width: 100%;
  overflow: hidden;
  height: 15rem;
  flex-shrink: 0;
  object-fit: cover;
}
.forbes {
  position: relative;
  text-transform: uppercase;
  font-weight: 600;
}
.minRead {
  position: relative;
  letter-spacing: -0.04em;
}
.forbesParent,
.minReadWrapper {
  display: flex;
  flex-direction: row;
}
.minReadWrapper {
  border-radius: var(--br-7xs);
  background-color: var(--color-whitesmoke);
  padding: var(--padding-10xs) var(--padding-7xs);
  align-items: flex-start;
  justify-content: flex-start;
  font-size: var(--text-medium-size);
  color: var(--color-dimgray);
}
.forbesParent {
  align-self: stretch;
  align-items: center;
  justify-content: space-between;
}
.bestAffiliateMarketing {
  margin: 0;
  align-self: stretch;
  position: relative;
  font-size: var(--font-size-7xl);
  letter-spacing: 0.02em;
  line-height: 2.13rem;
  font-weight: 700;
  font-family: var(--font-alegreya);
  color: var(--studio-darkmode-exportbg-1a202e);
}
.frameContainer {
  align-self: stretch;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  gap: var(--gap-9xs);
}
.timeToLevel {
  align-self: stretch;
  position: relative;
  letter-spacing: 0.01em;
  line-height: 120%;
}
.frameGroup,
.timeToLevelUpYourContentWrapper {
  align-self: stretch;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
}
.timeToLevelUpYourContentWrapper {
  color: var(--studio-darkmode-exportbg-1a202e);
  font-family: var(--font-roboto);
}
.frameGroup {
  gap: var(--gap-5xs);
}
.frameItem {
  align-self: stretch;
  position: relative;
  border-top: 0.7px solid var(--studio-lightmode-lightseparator-ecf0f6);
  box-sizing: border-box;
  height: 0.04rem;
}
.avatarIcon {
  position: relative;
  border-radius: 50%;
  width: 2.5rem;
  height: 2.5rem;
  object-fit: cover;
}
.annaRos,
.postedJustNow {
  position: relative;
  display: inline-block;
  width: 9.09rem;
}
.annaRos {
  letter-spacing: -0.04em;
  font-weight: 600;
}
.postedJustNow {
  font-size: var(--text-medium-size);
  font-family: var(--font-roboto);
  color: var(--studio-darkmode-normalsolid-icons-96a2be);
}
.articleAuthor,
.authorText {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  gap: var(--gap-11xs);
}
.articleAuthor {
  flex-direction: row;
  align-items: center;
  gap: var(--gap-3xs);
}
.frameParent,
.lineParent,
.rectangleParent {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}
.lineParent {
  align-self: stretch;
  justify-content: flex-start;
  gap: var(--gap-smi);
  font-size: var(--font-size-mid);
  color: var(--color-dimgray);
}
.frameParent,
.rectangleParent {
  flex: 1;
}
.frameParent {
  align-self: stretch;
  padding: var(--padding-base);
  justify-content: space-between;
}
.rectangleParent {
  border-radius: var(--br-3xs);
  background-color: var(--dominant);
  border: 0.7px solid var(--studio-lightmode-midseparator-dee3eb);
  box-sizing: border-box;
  height: 29.44rem;
  overflow: hidden;
  justify-content: flex-start;
  gap: var(--gap-9xs);
  text-align: left;
  font-size: var(--text-large-size);
  color: var(--studio-darkmode-maincta-457eff);
  font-family: var(--font-source-code-pro);
}
@media screen and (max-width: 1200px) {
  .rectangleParent {
    height: auto;
  }
}
@media screen and (max-width: 960px) {
  .rectangleParent {
    flex: unset;
    align-self: stretch;
  }
}
@media screen and (max-width: 420px) {
  .rectangleParent {
    align-self: stretch;
    width: auto;
    height: auto;
    align-items: center;
    justify-content: flex-start;
    padding-left: 0;
    padding-bottom: 0;
    box-sizing: border-box;
    flex: unset;
  }
}
