.payola {
  font-weight: 700;
  color: var(--color-orange);
}
.signUpForContainer {
  margin: 0;
  position: relative;
  font-size: inherit;
  line-height: 120%;
  font-family: inherit;
}
.payola1 {
  font-family: var(--text-small);
}
.payolaIsAWorldClassENew {
  margin: 0;
}
.everythingUxIsContainer {
  align-self: stretch;
  position: relative;
  font-size: var(--text-large-size);
  line-height: 1.5rem;
}
.inputFieldParent,
.signUpForPayolaParent {
  align-self: stretch;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
  gap: var(--gap-5xs);
}
.inputFieldParent {
  justify-content: center;
  gap: var(--gap-base);
}
.button1 {
  position: relative;
  font-size: var(--text-large-size);
  line-height: 1.5rem;
  font-family: var(--text-small);
  color: var(--dominant);
  text-align: center;
}
.button {
  cursor: pointer;
  border: 0;
  padding: var(--padding-sm) var(--padding-9xl);
  background-color: var(--color-orange);
  border-radius: var(--br-7xs);
  overflow: hidden;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
}
.button:hover {
  background-color: var(--color-lightseagreen);
}
.frameGroup,
.frameParent,
.newletterSignUp {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.frameGroup {
  width: 100%;
  border-radius: var(--br-3xs);
  background-color: var(--dominant);
  padding: var(--padding-xl);
  box-sizing: border-box;
  justify-content: center;
  gap: var(--gap-base);
  max-width: 28.13rem;
}
.frameParent,
.newletterSignUp {
  justify-content: flex-start;
}
.frameParent {
  width: 100%;
  height: 25.38rem;
  gap: var(--gap-xl);
  max-width: 37.5rem;
}
.newletterSignUp {
  align-self: stretch;
  border-radius: var(--br-5xs);
  height: 31.63rem;
  overflow: hidden;
  flex-shrink: 0;
  padding: var(--padding-xl) var(--padding-21xl);
  box-sizing: border-box;
  background-image: url(/public/newletter-sign-up@3x.png);
  background-size: cover;
  background-repeat: no-repeat;
  background-position: top;
  text-align: center;
  font-size: var(--font-size-21xl);
  color: var(--dark-gray);
  font-family: var(--text-small);
}
@media screen and (max-width: 960px) {
  .frameGroup {
    min-width: 15.63rem;
  }
}
@media screen and (max-width: 420px) {
  .frameGroup {
    width: 80vw;
    left: 0;
  }
  .frameParent {
    flex: 1;
  }
  .newletterSignUp {
    height: 43.75rem;
    padding-bottom: var(--padding-81xl);
    box-sizing: border-box;
  }
}
