.span {
  letter-spacing: -0.08em;
}
.seeFullLibraryContainer {
  position: relative;
  display: inline-block;
  width: 79.63rem;
  flex-shrink: 0;
}
.brainTheatre,
.contentSection {
  align-self: stretch;
  display: flex;
}
.contentSection {
  flex-direction: row;
  flex-wrap: wrap;
  padding: var(--padding-11xl);
  align-items: center;
  justify-content: space-between;
}
.brainTheatre {
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  text-align: left;
  font-size: var(--font-size-lg);
  color: var(--dominant);
  font-family: var(--font-roboto);
}
@media screen and (max-width: 1200px) {
  .contentSection {
    gap: var(--gap-0);
    padding: 0;
    box-sizing: border-box;
  }
}
@media screen and (max-width: 960px) {
  .contentSection {
    flex: 1;
    flex-direction: column;
  }
}
@media screen and (max-width: 420px) {
  .brainTheatre,
  .contentSection {
    align-self: stretch;
    width: auto;
    flex-direction: column;
    justify-content: flex-start;
    box-sizing: border-box;
  }
  .contentSection {
    flex: 1;
    gap: var(--gap-14xl);
    align-items: flex-start;
    padding: 0;
  }
  .brainTheatre {
    align-items: center;
    padding-left: var(--padding-mini);
    max-width: 25rem;
  }
}
