@keyframes jello-horizontal {
  0%,
  to {
    transform: scale3d(1, 1, 1);
  }
  30% {
    transform: scale3d(1.25, 0.75, 1);
  }
  40% {
    transform: scale3d(0.75, 1.25, 1);
  }
  50% {
    transform: scale3d(1.15, 0.85, 1);
  }
  65% {
    transform: scale3d(0.95, 1.05, 1);
  }
  75% {
    transform: scale3d(1.05, 0.95, 1);
  }
}
.learnMore,
.resources {
  position: relative;
  display: inline-block;
  width: 10.77rem;
  flex-shrink: 0;
}
.learnMore:hover {
  color: var(--color-orange);
  animation: 2.5s ease-in-out 0s 1 normal both jello-horizontal;
  opacity: 1;
  font-weight: 700;
  text-decoration: underline;
}
.affiliateProgramsContainer:hover,
.ourFuture:hover,
.resources:hover {
  color: var(--color-orange);
  animation: 2.5s ease-in-out 0s 1 normal none jello-horizontal;
  opacity: 1;
  font-weight: bolder;
  text-decoration: underline;
}
.affiliatePrograms {
  margin: 0;
}
.affiliateProgramsContainer {
  text-decoration: none;
  color: inherit;
}
.affiliateProgramsContainer:hover,
.ourFuture:hover {
  font-weight: 700;
}
.affiliateProgramsContainer,
.ourFuture {
  position: relative;
  display: inline-block;
  width: 10.77rem;
  flex-shrink: 0;
}
.learnMoreParent {
  position: absolute;
  top: 0;
  left: 0;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  gap: var(--gap-5xl);
}
.frameWrapper {
  position: relative;
  width: 47.56rem;
  height: 2.38rem;
}
.frameChild {
  position: absolute;
  height: 72%;
  width: 70.27%;
  top: 4%;
  right: 14.87%;
  bottom: 24%;
  left: 14.86%;
  max-width: 100%;
  overflow: hidden;
  max-height: 100%;
  object-fit: cover;
}
.frame {
  cursor: pointer;
  border: 0;
  padding: 0;
  background-color: transparent;
  position: relative;
  width: 2.19rem;
  height: 1.56rem;
  overflow: hidden;
  flex-shrink: 0;
  display: none;
}
.frameParent,
.navBar {
  display: flex;
  flex-direction: row;
  align-items: center;
}
.frameParent {
  width: 47.56rem;
  justify-content: center;
  gap: var(--gap-5xl);
}
.navBar {
  background-color: var(--color-gray-900);
  width: 100vw;
  height: 4.81rem;
  padding: var(--padding-3xl) var(--padding-69xl);
  box-sizing: border-box;
  justify-content: space-between;
  position: sticky;
  top: 0;
  text-align: center;
  font-size: var(--text-large-size);
  color: var(--dominant);
  font-family: var(--text-small);
}
@media screen and (max-width: 1200px) {
  .navBar {
    padding-left: var(--padding-21xl);
    padding-right: var(--padding-21xl);
    box-sizing: border-box;
  }
}
@media screen and (max-width: 960px) {
  .frameWrapper {
    display: none;
  }
  .frame {
    display: flex;
  }
  .frameParent {
    display: flex;
    align-items: flex-end;
    justify-content: center;
    padding-left: 75%;
    padding-right: 0;
    box-sizing: border-box;
  }
  .navBar {
    padding-left: var(--padding-5xl);
    padding-right: var(--padding-5xl);
    box-sizing: border-box;
  }
}
@media screen and (max-width: 420px) {
  .frameParent {
    padding-left: 65%;
  }
}
