.email,
.icon {
  position: relative;
}
.email {
  align-self: stretch;
  line-height: 22px;
}
.icon {
  width: 18px;
  height: 18px;
  overflow: hidden;
  flex-shrink: 0;
  object-fit: cover;
}
.jeraldinelocofyuiai {
  border: 0;
  font-family: var(--text-small);
  font-size: var(--text-large-size);
  background-color: transparent;
  flex: 1;
  position: relative;
  line-height: 24px;
  color: var(--gray);
  text-align: left;
}
.iconParent {
  align-self: stretch;
  border-radius: var(--default-border-raius);
  background-color: var(--dominant);
  border: 1px solid var(--light-border);
  display: flex;
  flex-direction: row;
  padding: var(--padding-3xs) var(--padding-sm);
  align-items: center;
  justify-content: flex-start;
  gap: var(--gap-3xs);
}
.thisIsA {
  align-self: stretch;
  position: relative;
  font-size: var(--text-small-size);
  line-height: 20px;
  color: var(--gray);
}
.typenormalStatedefault {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  gap: var(--gap-7xs);
  text-align: left;
  font-size: var(--text-medium-size);
  color: var(--dark-gray);
  font-family: var(--text-small);
  align-self: stretch;
}
