.vectorIcon {
  position: relative;
  width: 20.11rem;
  height: 3.44rem;
}
.gameIconstheater {
  flex: 1;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.thePayolaBrain {
  position: relative;
  font-weight: 200;
  background: linear-gradient(180deg, #ef9e00, rgba(17, 17, 17, 0));
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}
.levelUpYour {
  margin: 0;
}
.levelUpYourContainer {
  position: relative;
  font-size: var(--font-size-mini);
  font-weight: 200;
  text-shadow: 0 4px 4px rgba(0, 0, 0, 0.25);
}
.thePayolaBrainTheatreParent {
  flex: 1;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
  gap: var(--gap-3xs);
}
.vectorIcon1 {
  position: relative;
  width: 20.11rem;
  height: 3.44rem;
  object-fit: cover;
}
.gameIconstheaterParent {
  align-self: stretch;
  border-bottom: 1px dashed var(--color-orange);
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  padding: 0 0 var(--padding-16xl);
  align-items: center;
  justify-content: flex-start;
  gap: var(--gap-61xl);
  text-align: center;
  font-size: var(--font-size-16xl);
  color: var(--dominant);
  font-family: var(--text-small);
}
@media screen and (max-width: 1200px) {
  .gameIconstheaterParent {
    flex-direction: row;
    gap: var(--gap-61xl);
    align-items: center;
    justify-content: flex-start;
  }
}
@media screen and (max-width: 960px) {
  .gameIconstheater,
  .thePayolaBrainTheatreParent {
    flex: unset;
    align-self: stretch;
  }
  .gameIconstheaterParent {
    flex-direction: column;
    gap: var(--gap-31xl);
    align-items: center;
    justify-content: flex-start;
  }
}
@media screen and (max-width: 420px) {
  .vectorIcon {
    display: none;
  }
  .gameIconstheater,
  .thePayolaBrainTheatreParent {
    flex: unset;
    align-self: stretch;
  }
  .gameIconstheaterParent {
    flex-direction: column;
    gap: var(--gap-31xl);
    align-items: center;
    justify-content: flex-start;
  }
}
