.frameChild,
.frameInner,
.frameItem {
  position: absolute;
  top: 0;
  background-color: var(--color-dodgerblue-200);
  height: 3.75rem;
}
.frameChild {
  left: 0;
  border-radius: var(--br-11xl);
  width: 17rem;
}
.frameInner,
.frameItem {
  left: 12.19rem;
  border-radius: var(--br-13xl);
  width: 22.25rem;
}
.frameInner {
  left: 29.13rem;
  border-radius: var(--br-11xl);
  width: 22.69rem;
}
.optionsBarChild,
.rectangleParent {
  position: absolute;
  top: 0.25rem;
  left: 0.38rem;
  width: 51.81rem;
  height: 3.75rem;
}
.optionsBarChild {
  border-radius: var(--br-11xl);
  background-color: var(--color-gray-600);
  width: 17.38rem;
}
.programStats,
.programSummary,
.userReviews {
  position: absolute;
  display: flex;
  align-items: center;
  justify-content: center;
}
.programSummary {
  height: 100%;
  width: 33.21%;
  top: 0;
  left: 0;
  text-decoration: underline;
}
.programStats,
.userReviews {
  height: 93.75%;
  width: 33.33%;
  top: 6.25%;
  left: 33.21%;
  -webkit-text-stroke: 0.5px rgba(0, 0, 0, 0.1);
}
.userReviews {
  height: 100%;
  width: 33.45%;
  top: 0;
  left: 66.55%;
}
.optionsBar {
  position: absolute;
  top: 27.5rem;
  left: 25.75rem;
  width: 52.31rem;
  height: 4rem;
  text-align: center;
  font-size: var(--font-size-xl);
  color: var(--dominant);
  font-family: var(--font-roboto);
}
