.contentSection,
.contentSectionWrapper {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
}
.contentSection {
  flex: 1;
  background-color: var(--dominant);
  overflow-x: auto;
  padding: var(--padding-11xl);
  align-items: flex-start;
  gap: var(--gap-6xl);
}
.contentSectionWrapper {
  width: 83.38rem;
  padding: var(--padding-14xl) 0 0;
  box-sizing: border-box;
  align-items: center;
}
.moreFeaturedContent {
  text-decoration: none;
  flex: 1;
  position: relative;
  font-style: italic;
  color: inherit;
}
.featuredContent,
.moreFeaturedContentWrapper {
  align-self: stretch;
  display: flex;
  justify-content: center;
}
.moreFeaturedContentWrapper {
  border-bottom: 0.5px solid var(--color-gray-1000);
  flex-direction: row;
  padding: 0 var(--padding-14xl) var(--padding-xl) var(--padding-23xl);
  align-items: center;
}
.featuredContent {
  overflow: hidden;
  flex-direction: column;
  align-items: flex-start;
}
.brainTheatreChild {
  position: relative;
  width: 7.81rem;
  height: 7rem;
  object-fit: cover;
}
.brainTheatre,
.resources {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.brainTheatre {
  align-self: stretch;
  background-color: var(--color-gray-1100);
  padding: var(--padding-21xl) 0 0;
  justify-content: center;
  gap: var(--gap-21xl);
}
.resources {
  position: relative;
  background-color: var(--dominant);
  width: 100%;
  overflow: hidden;
  padding: 0 3.19rem 0 var(--padding-36xl);
  box-sizing: border-box;
  justify-content: flex-start;
  gap: 6.19rem;
  text-align: left;
  font-size: var(--font-size-xl);
  color: var(--color-orange);
  font-family: var(--text-small);
}
@media screen and (max-width: 960px) {
  .contentSection {
    flex-direction: row;
  }
  .moreFeaturedContent {
    font-size: var(--font-size-mini);
  }
}
@media screen and (max-width: 420px) {
  .contentSection {
    flex-direction: row;
    gap: var(--gap-xl);
    align-items: flex-start;
    justify-content: flex-start;
    padding-left: var(--padding-mini);
    padding-top: var(--padding-xl);
    padding-right: 0;
    box-sizing: border-box;
  }
  .brainTheatre,
  .resources {
    width: auto;
    align-items: center;
    justify-content: flex-start;
  }
  .brainTheatre {
    align-self: stretch;
    gap: var(--gap-0);
  }
  .resources {
    align-self: unset;
    padding-left: 0;
    padding-right: 0;
    box-sizing: border-box;
  }
}
