  position: relative;
  object-fit: cover;
}
.locouiLogoIcon {
  border-radius: var(--br-5xs);
  width: 34.01px;
  height: 34.01px;
  overflow: hidden;
  flex-shrink: 0;
}
.locouiIcon {
  width: 57.28px;
  height: 13.56px;
}
