.istockphoto1170923792612x612Icon {
  align-self: stretch;
  flex: 1;
  position: relative;
  border-radius: var(--br-5xs);
  max-width: 100%;
  overflow: hidden;
  max-height: 100%;
  object-fit: cover;
}
.badass1 {
  font-family: var(--text-small);
}
.badass1,
.span {
  font-weight: 600;
}
.badass {
  margin: 0;
}
.resources {
  margin: 0;
  font-weight: 600;
}
.title {
  align-self: stretch;
  position: relative;
  text-shadow: -2px 2px 1px rgba(0, 0, 0, 0.25);
}
.a {
  font-weight: 300;
}
.subtitle {
  align-self: stretch;
  font-size: var(--font-size-xl);
  color: var(--color-black);
}
.checkItOut,
.subtitle {
  position: relative;
}
.checkItOutWrapper {
  border-radius: var(--br-5xs);
  background-color: var(--color-gray-1000);
  box-shadow: 0 4px 4px rgba(0, 0, 0, 0.25);
  overflow: hidden;
  display: flex;
  flex-direction: row;
  padding: var(--padding-3xs) var(--padding-6xl);
  align-items: center;
  justify-content: flex-start;
}
.titleAndSubInner {
  flex-direction: column;
  font-size: var(--font-size-lg);
  color: var(--dominant);
}
.heroFrame,
.titleAndSub,
.titleAndSubInner {
  align-self: stretch;
  display: flex;
  align-items: center;
  justify-content: flex-start;
}
.titleAndSub {
  flex: 1;
  border-top: 1px dashed var(--color-black);
  border-right: 1px dashed var(--color-black);
  border-bottom: 1px dashed var(--color-black);
  flex-direction: column;
  padding: var(--padding-56xl) var(--padding-11xl);
  gap: var(--gap-16xl);
}
.heroFrame {
  border-bottom: 5px dashed var(--color-orange);
  flex-direction: row;
  padding: 0 var(--padding-40xl) var(--padding-66xl) var(--padding-66xl);
  text-align: center;
  font-size: var(--font-size-21xl);
  color: var(--color-orange);
  font-family: var(--text-small);
}
@media screen and (max-width: 420px) {
  .istockphoto1170923792612x612Icon,
  .titleAndSub {
    flex: unset;
    align-self: stretch;
  }
  .titleAndSub {
    gap: var(--gap-16xl);
    padding-top: var(--padding-6xl);
    padding-bottom: var(--padding-6xl);
    box-sizing: border-box;
    border: 1px dashed var(--color-black);
  }
  .heroFrame {
    flex-direction: column;
    padding-left: var(--padding-mini);
    padding-right: var(--padding-mini);
    padding-bottom: var(--padding-66xl);
    box-sizing: border-box;
  }
}
