.latestArticles {
  margin: 0;
  position: relative;
  font-size: inherit;
  line-height: 120%;
  font-weight: 700;
  font-family: inherit;
}
.featuredContentTo {
  position: relative;
  font-size: var(--text-large-size);
  line-height: 1.5rem;
  font-weight: 600;
}
.latestArticlesVideosParent {
  width: 90.13rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
  gap: var(--gap-6xs);
}
.contentSection,
.frameParent {
  display: flex;
  flex-direction: row;
  box-sizing: border-box;
  align-items: flex-start;
  justify-content: center;
}
.contentSection {
  flex: 1;
  background-color: var(--dominant);
  height: 33.19rem;
  padding: var(--padding-11xl);
  gap: var(--gap-29xl);
  text-align: left;
  font-size: var(--text-large-size);
  color: var(--studio-darkmode-maincta-457eff);
  font-family: var(--font-source-code-pro);
}
.frameParent {
  align-self: stretch;
  height: 43.94rem;
  flex-wrap: wrap;
  padding: var(--padding-13xl) 0 0;
  gap: var(--gap-mini);
  text-align: center;
  font-size: var(--font-size-21xl);
  color: var(--dark-gray);
  font-family: var(--text-small);
}
@media screen and (max-width: 960px) {
  .contentSection {
    align-self: stretch;
    height: auto;
    flex-direction: column;
  }
  .frameParent {
    height: auto;
    flex-direction: row;
    align-items: center;
    justify-content: flex-start;
    padding-left: var(--padding-181xl);
    padding-top: var(--padding-131xl);
    box-sizing: border-box;
    max-width: 40.63rem;
  }
}
@media screen and (max-width: 420px) {
  .latestArticles {
    line-height: 3.13rem;
    text-align: center;
  }
  .latestArticlesVideosParent {
    padding-top: var(--padding-381xl);
    padding-bottom: var(--padding-31xl);
    box-sizing: border-box;
  }
  .contentSection {
    flex: 1;
    align-self: stretch;
    height: auto;
    flex-direction: column;
    gap: var(--gap-14xl);
    max-width: 22.19rem;
  }
  .frameParent {
    padding-left: 0;
    padding-top: var(--padding-31xl);
    box-sizing: border-box;
  }
}
